import { Component, OnInit, EventEmitter, OnDestroy, Output  } from '@angular/core';
import { Subject, Subscription } from 'rxjs';
import { debounceTime, distinctUntilChanged, map, tap } from 'rxjs/operators';
import { SearchBarService } from '../../services/search-bar.service';

@Component({
  selector: 'app-search-bar',
  templateUrl: './search-bar.component.html',
  styleUrls: ['./search-bar.component.scss']
})
export class SearchBarComponent implements OnDestroy {

  @Output() searchChanged: EventEmitter<string> = new EventEmitter<string>(); //On crer un venement de sortie de type string

  //On crer un Sujet de String pour ragir diffremment lorsque l'utilisateur tape dans le champs de saisie
  debouncer: Subject<string> = new Subject<string>();

    // On cre un attribut ref pour contenir l'adresse mmoire de l'couteur qui s'abonne  un Observable qui l'on initailise  null
    ref: Subscription = null;
  constructor(public searchb: SearchBarService) {
    this.searchb.show;
    this.ref = this.debouncer.pipe(
      //N'envoies pas d'information tant que la chane est identique  celle prcdemment tape
      distinctUntilChanged(), 

      //Ajoute un dlai de 250 millisecondes entre chaque valeur
      debounceTime(250), 

      //Permet d'ffectuer un traitement pour chaque valeur, ici on envoie la chane qui est tape
      tap(q => this.searchChanged.emit(q))
    ).subscribe();
   }

  // tslint:disable-next-line: member-ordering
  public searchDoctors: string; // Typer le type de la searchbar
  searchDoctorsUpdate = new Subject<string>(); // a chaque clique sur le clavier

  ngOnDestroy() {
    //Scurit pour viter les fuites mmoire, on nttoie l'dresse mmoire qui correspond  l'couteur utiliser lors d'un .subscribe()
    if (!!this.ref) {
      this.ref.unsubscribe();
    }
  }

  search(q: string): void {
    //On envoie une nouvelle information  notre Sujet
    this.debouncer.next(q);
  }

}
