import { MatSort } from '@angular/material/sort';
import { MatTable } from '@angular/material/table';
import { TableDataSource, TableItem } from './table-datasource';
import { SearchBarService } from '../../../services/search-bar.service';
import { Router } from '@angular/router';
import { UserService } from '../../../api/services/user.service';
import { UserDto } from '../../../api/models/user-dto';

@Component({
  selector: 'app-table',
  templateUrl: './table.component.html',
  styleUrls: ['./table.component.scss']
})
export class TableComponent implements OnInit {

  constructor(public searchb: SearchBarService,
    private router: Router,
    private userService: UserService
    ) { }

searchTerm: string;

title = 'Medecins';

medecinsList: UserDto[] = [];

displayedMedecins = []; // La liste qui est lie  la vue (celle qui est affiche)
paginatorInfo: PageEvent = {pageSize: 5, pageIndex: 0, length: this.medecinsList.length}; // Les informations que l'on lie au paginateur

ngOnInit() {
// this.searchb.show();
// tslint:disable-next-line: max-line-length
this.userService.getUserDoctors().subscribe(doctors => {
this.medecinsList = doctors as UserDto[],
this.displayedMedecins = this.medecinsList;
this.paginatorInfo.length = this.displayedMedecins.length;
// this.displayedMedecins = this.paginateElements<UserDto>(this.medecinsList, this.paginatorInfo);
// tslint:disable-next-line: max-line-length
this.displayedMedecins = this.paginateElements<UserDto>(this.displayedMedecins, this.paginatorInfo); // On pagine nos lments qui correspondent  la recherche
});
}

// Mthode dclenche lorsqu'une recherche est faite dans notre composant de recherche
search(query: string): void {
// Si la recherche est vide on affecte tous les lments  la liste que l'on affiche
this.displayedMedecins = this.medecinsList;

// Sinon on filtre les lments dont le nom ou le prnom ne commence pas par la chane recherche
if (query !== '') {
this.displayedMedecins = this.medecinsList.filter((doctor) => {

const len = query.length; // On rcupre la taille de la chane recherche
// tslint:disable-next-line: max-line-length
const firstname = doctor.firstName.trim().substr(0, len).toLocaleLowerCase(); // On cre une sous chane du prnom de la mme taille que celle recherche
const lastname = doctor.lastName.trim().substr(0, len).toLowerCase(); // Idem ave cle nom

// On vrifie ensuite l'galit des chanes (on transforme ces chanes en minuscule pour ne pas tre sensible  la casse)
const firstNameMatched = firstname === query.toLowerCase();
const lastNameMatched = lastname === query.toLowerCase();

// On conserve les lments si la sous-chane cre avec le prnom ou celle cre avec le nom correspond
return firstNameMatched || lastNameMatched;
});
}

this.paginatorInfo.pageIndex = 0; // On remet le paginateur  la premire page
this.paginatorInfo.length = this.displayedMedecins.length; // On affecte la taille des lments trouvs  la taille du paginateur
// tslint:disable-next-line: max-line-length
this.displayedMedecins = this.paginateElements<UserDto>(this.displayedMedecins, this.paginatorInfo); // On pagine nos lments qui correspondent  la recherche
}

// Mthode dclenche lorsque l'utilisateur change de page ou change la taille du paginateur
pageChange(event: PageEvent): void {
this.paginatorInfo = event; // On met  jour la variable qui contient les informations du paginateur
this.displayedMedecins = this.paginateElements<UserDto>(this.medecinsList, this.paginatorInfo); // On pagine nos lments affichs
}

/*Mthode paramtrique qui permet de paginer des lments de n'importe quel type
Prend en paramtre un tableau d'lment et un paginateur
Retourne un tableau d'lment du mme type
*/
paginateElements<T>(elements: T[], paginator: PageEvent): T[] {
return elements.filter((element, index) => {
const start = paginator.pageIndex * paginator.pageSize; // On construit l'indice de dpart.
const end = start + paginator.pageSize - 1; // On construit l'indice de fin.
return index >= start && index <= end; // On conserve que les lments qui sont compris entre les indices de dpart et de fin.
});
}

fiche(id: any) {
this.router.navigate(['medecins/user', { id }]);
console.log(id);
}

deleteUser(e: number) {
this.userService.deleteUserId(e).subscribe( x => {
  this.ngOnInit();
});
}

}
